import React from 'react';
import "./header.scss";

import Button from './../Components/Button'

import coin from './../Components/images/coin.svg'
import iconMenu from "./images/icon-menu.svg"

import ButtonControls from "./ButtonControls/ButtonControls"


function Header(props) {
    return (<header className="header">

            <div className="header_controls header_userControls">
                <div className="container">
                <ButtonControls UserHasRegistered={true}/>

                <div className="header_userInfo">
                    <div className="coin_block">
                        <img className="coin_img" src={coin} alt="coin"/>
                        <span className="coin_value">108 987</span>
                    </div>
                    <Button className="btn_navigation" onClick={e => props.showPopup()} >
                        <img src={iconMenu} alt="navigation"/>
                    </Button>
                </div>
                </div>
            </div>
            <div className="header_controls header_authorization">
                <div className="container">
                    <Button className="btn btn_enter" onClick={() => {
                        console.log("enter");
                    }}><span>Войти</span></Button>
                    <Button className="btn btn_registration" onClick={() => {
                        console.log("registration");
                    }}>Зарегистрироваться</Button>
                </div>
            </div>

    </header>)
}


export default Header