import React from 'react'

import './../common/reset.css'
import '../common/base.scss'

import Header from './Header/Header';
import Menu from './Menu/Menu'
import Main from './Main/Main';

import GameFrame from './GameFrame/GameFrame'

import PopupWindow from './Popups/PopupWindow';
import {BrowserRouter as Router} from "react-router-dom"


/// TODO Dirty data
import users from "./../data/users"
class App extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            users : users,
            popupStatus: false,
            gameShowStatus: false,
            games: props.state.games,
            information: props.state.information,
            policy: props.state.policy,
            isUserRegistered: true,
        };
    }


    popupClose = () => {
        this.setState({popupStatus: false})
    };
    popupShow = () => {
        this.setState({popupStatus: true})
    };

    playGame = () => {
        this.setState({gameShowStatus: true})
    };

    closeGame = () => {
        this.setState({gameShowStatus: false})
    };

    logOut = () => {
      this.setState({isUserRegistered: false})
    };


    render() {

        return (
            <Router  basename={process.env.PUBLIC_URL + "/"}>
                <div className='wrapperBlock'>

                        {this.state.gameShowStatus ?
                            <GameFrame closeGame={this.closeGame}/>
                            :
                            <div className='wrapper'>
                                <Header showPopup={this.popupShow}/>
                                <Menu/>
                                <Main
                                    games={this.state.games}
                                    information ={this.state.information}
                                    playGame={this.playGame}
                                    policy={this.state.policy}
                                    users={this.state.users}
                                />
                            </div>
                        }

                        {this.state.popupStatus ? <PopupWindow
                            closePopup={this.popupClose}
                            isUserRegistered={this.state.isUserRegistered}
                            functionLogOut = {this.logOut}


                        /> : null}


                </div>
            </Router>
        )
    }


}


export default App