import React from 'react';
import Button from './../../Components/Button'
import Image from './../../Components/Image'

import imageChest from "./../../Components/images/bonuses/chest.jpg"



import "./sourcesOfCoins.scss"


function SourcesOfCoins(props) {

    let chestsUnused = 2;






    return (
        <div>
            <h1>Источники фишек</h1>
            <div className="source source_unused">
                { (chestsUnused>0) ? (
                    <Button className="source_chest">
                    <Image className="source_chest__img"  src={imageChest} alt={"Сундук"} width={340} height={190} />
                    <p className="source_chest__text">Сундуков доступно: {chestsUnused}</p>
                    </Button>)
                    : null}

                <ul className="source_list">
                    <li className="source_item">
                        <Button className="source_item__button">
                            <p className="source_item__text">Подписаться на уведомления</p>
                            <p className="source_item__val">+{200}</p>
                        </Button>
                    </li>

                    <li className="source_item">
                        <Button className="source_item__button">
                            <p className="source_item__text">Установить приложение</p>
                            <p className="source_item__val">+{500}</p>
                        </Button>
                    </li>

                    <li className="source_item">
                        <Button className="source_item__button">
                            <p className="source_item__text">Поделиться в соцсетях</p>
                            <p className="source_item__val">+{500}</p>
                        </Button>
                    </li>

                    <li className="source_item">
                        <Button className="source_item__button">
                            <p className="source_item__text">Посмотреть рекламу</p>
                            <p className="source_item__val">+{200}</p>
                        </Button>
                    </li>
                </ul>
            </div>



            <span className="source_completed">Выполнено</span>


            <div className="source source_used">

                <div className="source_chest" disabled={true}>
                    <Image className="source_chest__img"  src={imageChest} alt={"Сундук"} width={340} height={190} />
                    <p className="source_chest__text">Сундуков доступно: 0</p>
                </div>


                <ul className="source_list">
                    <li className="source_item">
                        <div className="source_item__button" disabled={true}>
                            <p className="source_item__text">Посмотреть рекламу</p>
                            <p className="source_item__val">+{200}</p>
                        </div>
                    </li>
                </ul>
            </div>




        </div>
    )

}

export default SourcesOfCoins