import React from "react";


import users from "../../../data/users"
import rank from "../../../data/ranks"


import defaultAvatar from "./../../../App/Components/images/rank/0.png"

function UnRegisteredUser(props) {
    return (

        <div className="user_control">
            <img  className="user_control__img" width="40" height="40" src={defaultAvatar} alt="unregistred" />
            <span className="user_control__name">Nickname</span>
        </div>

    )
}

class RegisteredUser extends React.Component {

    render() {
        const userId = 1;
        const user = this.props.users[userId-1];
        const userName =  user.name;

        const currentUserRankID = user.rank;
        let userRank = rank[currentUserRankID].name;
        let userAvatarUrl = rank[currentUserRankID].urlImg;

        return (
            <div className="user_control">
                <div className={"user_control"}>
                    <img  className="user_control__img" src={userAvatarUrl} alt={userRank} width="40" height="40" />
                    <span className="user_control__name">{userName}</span>
                </div>
            </div>
        )
    }
}



function ButtonControls(props) {

    let userHasRegistered = props.userHasRegistered;
    userHasRegistered = true;

    return userHasRegistered ? <RegisteredUser users={users} /> : <UnRegisteredUser/>

}

export default ButtonControls;