import React from "react"
import Button from "./../../../Components/Button"
import "./proposal.scss"

import bonusUrl from "./../../../Components/images/icon-bonus.svg"

function Proposal(props) {



    return(
        <div className="section section_proposal">
            <p className="proposal_text">Установи наше преложение и получи</p>
            <div className="proposal_param">
                <img className="proposal_img" src={bonusUrl} alt="bonus"/>
                <span className="proposal_val">1 000 фишек</span>
            </div>
            <Button className="btn btn_getPWA">Установить</Button>
        </div>
    );
}

export default Proposal