import React from 'react';
import Details from './../../Components/Details'
import parse from 'html-react-parser'


import {NavLink} from "react-router-dom"

import "./informations.scss"


function InformationPage(props) {

    const {information} = props;

    return (
        <div>
            <h1 className="info_title">Информация</h1>

            <div className="info_block">
                <NavLink className="link_pp" to="/privacy-policy/">Наша политика конфиденциальности</NavLink>
                <Details className="info_details" title={"О проекте"} children={parse(information.aboutProject)} />
                <Details className="info_details" title={"Турниры"} children={parse(information.tournaments)} />
                <Details className="info_details" title={"Фишки"} children={parse(information.chips)} />
                <Details className="info_details" title={"Уровни"} children={parse(information.ranks)} />
                <Details className="info_details" title={"Популярние вопросы"} children={parse(information.popularQuestions)} />
                <Details className="info_details" title={"Контакты"} children={parse(information.contacts)} />
            </div>
        </div>
    )


}

export default InformationPage