import React from 'react';
import parse from 'html-react-parser'
import PropTypes from 'prop-types';

import Details from "./../../Components/Details"

import "./policy.scss"
import "./../../Components/seo.scss"

function PolicyPage(props) {

    const {policy} = props;


    return (
        <div className="bl_seo">
            <h1 className="policy_title">Защита информации</h1>
            <h2 className="policy_subtitle">Политика конфиденциальности и защита персональных данных</h2>
            <Details className="info_details" title={"Разглашение информации"} children={policy.policyText1}/>
            <Details className="info_details" title={"Рассылки и информационные сообщения"}
                     children={policy.policyText2}/>
            <h2>Какая информация о клиентах собирается на сайте Poperlo и как эта информация используется?</h2>
            <Details className="info_details" title={"Политика Poperlo в отношении личной информации."}
                     children={policy.policyText2}/>
            <Details className="info_details" title={"Политика конфиденциальности и защита персональных данных"}
                     children={policy.policyText2}/>
            <Details className="info_details" title={"Разглашение информации, полученной Poperlo"}
                     children={policy.policyText2}/>
            <div className="policy_block">
                <p>Poperlo несёт обязательства о защите данных в том виде, в котором Компания использует любую информацию,
                    собранную в ходе визита клиента на Веб-сайт. Вся обработка полученной от клиентов информации
                    производится строго в соответствии с политикой конфиденциальности.</p>

                <p>Предоставляя Poperlo информацию, клиент соглашается с правом на обработку Poperlo этой информации для
                    целей, описанных в настоящих условиях политики конфиденциальности.</p>

                <p>Poperlo сохраняет копии всей полученной от клиентов корреспонденции (включая копии всех электронных
                    писем) для того, чтобы в точности зарегистрировать всю полученную информацию.</p>
            </div>

        </div>
    )

}

PolicyPage.propTypes = {
    policy: PropTypes.string,
};


export default PolicyPage