import React from "react"
import slide from "../../../../../Components/images/tournaments/tournament-1.jpg";
import slideBig from "../../../../../Components/images/tournaments/tournament-1-big.jpg";

function SliderItem(props){
    let turnamentName = "турнир [НАЗВАНИЕ]";
    let scope = {
        splitterStyle: {
            width: "80%"
        }
    };
    return(
        <div className="slider_item">

            <picture className="slider_pic">
                    <source media="(max-width: 400px)" srcSet={slide}/>
                    <img className="slider_img" src={slideBig} alt={turnamentName}/>
            </picture>

            <div className="slider_wrapper">
                <p className="slider_dat">21 мар - 11 апр</p>
                <div className="slider_inner">
                    <p className="slider_name">{turnamentName}</p>
                    <div className="slider_center">
                        <p className="slider_text">призовой фонд</p>
                        <p className="slider_value">10 000 ₴</p>
                    </div>
                    <a className="btn link_tournament" href="#">Принять участие</a>
                </div>
                <div className="timer">
                    <div className="timer_block">
                        <span className="timer_name">До конца осталось:</span>
                        <p className="timer_value">2д : 8ч : 21м : 32с</p>
                    </div>
                    <div className="timer_rank">
                        <div className="timer_rank__line" style={scope.splitterStyle} />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SliderItem