import React, {useState} from 'react'
import Button from "./../../Components/Button"
import {NavLink} from 'react-router-dom'



import enterIcon from "./../../Components/images/info-menu/exit.svg"
import profileIcon from "./../../Components/images/info-menu/profile.svg"
import informationIcon from "./../../Components/images/info-menu/info.svg"
import coinsIcon from "./../../Components/images/info-menu/sources-of-chips.svg"


function InfoPopup(props) {

    const {isUserRegistered, closePopup, functionLogOut} = props;


    function UnRegistredUser(props) {
        return (
            <div className="popup_info">
                <nav className="popup_info__inner">
                    <Button className="item_popup" type="button">
                        <img className={"item_popup__img"} src={enterIcon} alt="Войти"/>
                        <span className={"item_popup_name"}>Войти</span>
                    </Button>
                    <Button className="item_popup" type="button">
                        <img className={"item_popup__img"} src={profileIcon} alt="Зарегистрироваться"/>
                        <span className={"item_popup_name"}>Зарегистрироваться</span>
                    </Button>
                    <NavLink className="item_popup" to={'/information/'} onClick={e =>  props.closePopup()}>
                        <img className={"item_popup__img"} src={informationIcon} alt="Информация"/>
                        <span className={"item_popup_name"}>Информация</span>
                    </NavLink>
                </nav>
            </div>
        )
    }

    function RegistredUser(props) {
        return (
            <div className="popup_info">
                <nav className="popup_info__inner">
                    <Button className="item_popup" type="button">
                        <img className={"item_popup__img"} src={profileIcon} alt="Профиль"/>
                        <span className={"item_popup_name"}>Профиль</span>
                    </Button>
                    <NavLink className="item_popup" to={'/information/'} onClick={e =>  props.closePopup()}>
                        <img className={"item_popup__img"} src={informationIcon} alt="Информация"/>
                        <span className={"item_popup_name"}>Информация</span>
                    </NavLink>
                    <NavLink className="item_popup" to={'/sources-of-coins/'} onClick={e =>  props.closePopup()}>
                        <img className={"item_popup__img"} src={coinsIcon} alt="Источники фишек"/>
                        <span className={"item_popup_name"}>Источники фишек</span>
                    </NavLink>
                    <Button className="item_popup" type="button" >
                        <img className={"item_popup__img"} src={enterIcon} onClick={e => props.functionLogOut()} alt="Выход"/>
                        <span className={"item_popup_name"}>Выход</span>
                    </Button>
                </nav>
            </div>
        )
    }


    function CheckUserStatus(isUserRegistered) {

        let showModal = isUserRegistered.isUserRegistered;

        return showModal ? <RegistredUser/> : <UnRegistredUser/>
    }

    return (
        <CheckUserStatus isUserRegistered={isUserRegistered} />
    );
}

export default InfoPopup;