import React from "react"
import InfoPopup from "./InfoPopup/InfoPopup";
import Button from "./../Components/Button"

import "./popupsSection.scss"

function PopupWindow(props) {

        const {closePopup, isUserRegistered, functionLogOut} = props;


        return (
            <div className="ShadowBlock">
                <div className="popupInner">
                    <Button className={"btn_closePopups"}  onClick={ e =>  props.closePopup()} >
                        <svg className={"btn_closePopups__icon"} xmlns="http://www.w3.org/2000/svg" x="0" y="0"
                             width="12" height="12" viewBox="0 0 12 12">
                            <path fill={"#3b2d62"}
                                  d="M11 .8c.3.3.3.8 0 1.2l-8.8 8.8c-.3.3-.8.3-1.2 0-.3-.3-.3-.8 0-1.2L9.8.8c.3-.3.8-.3 1.2 0z"/>
                            <path fill={"#3b2d62"}
                                  d="M1 .8c.3-.3.8-.3 1.2 0L11 9.6c.3.3.3.8 0 1.2-.3.3-.8.3-1.2 0L1 2C.7 1.7.7 1.2 1 .8z"/>
                        </svg>
                    </Button>
                    <InfoPopup
                        isUserRegistered={isUserRegistered}
                        closePopup={closePopup}
                        functionLogOut = {functionLogOut}
                    />
                </div>
            </div>
        )

}


export default PopupWindow;