import React from 'react';
import "./main.scss"

import HomePage from "./HomePage/HomePage"
import GamePage from "./GamePage/GamePage"
import TournamentsPage from "./TournamentsPage/TournamentsPage"
import InformationPage from "./InformationPage/InformationPage"
import PolicyPage from "./PolicyPage/PolicyPage"
import SourcesOfCoins from "./SourcesOfCoins/SourcesOfCoins"


import Error from "./404/NoMatchPage"


import {Switch, Route} from "react-router-dom"

function Main(props) {

    const {games, playGame, information, policy, users} = props;



    return (
        <main className="main_content">
            <div className="container">
                    <Switch>
                        <Route exact path="/" render={() => <HomePage games={games}/>}  />
                        <Route exact path="/tournaments/" render={() => <TournamentsPage games={games}/>} />
                        <Route exact path="/information/" render={() => <InformationPage information = {information} />} />
                        <Route exact path="/privacy-policy/" render={() => <PolicyPage policy = {policy} />} />
                        <Route exact path="/sources-of-coins/" render={() => <SourcesOfCoins policy = {policy} />} />
                        <Route  path="/:gameUrl" render={() => <GamePage games={games}  playGame={playGame} />} />
                        <Route component={Error} />
                    </Switch>
            </div>
        </main>
    )
}


export default Main